import { SPHttpClient, SPHttpClientResponse } from '@microsoft/sp-http';
import {ListItemFactory} from './ListItemFactory';
import {IListItem} from './models/IListItem';
import {IDirectoryListItem} from './models/IDirectoryListItem';

export class DirectoryListItemFactory extends ListItemFactory {
    public _getItems(requester: SPHttpClient, siteUrl: string, listName: string): IDirectoryListItem[] {
        let items: IDirectoryListItem[];
        requester.get(`${siteUrl}/_api/web/lists/getbytitle('${listName}')/items?$select=Title,Id`,
        SPHttpClient.configurations.v1,
        {
            headers: {
            'Accept': 'application/json;odata=nometadata',
            'odata-version': ''
            }
        })
        .then((response: SPHttpClientResponse): Promise<{ value: IDirectoryListItem[] }> => {
            return response.json();
        })
        .then((response: { value: IDirectoryListItem[] }): void => {
            items= response.value;
        });
        return items;
    }
}